.loader {
  background-color: var(--white-color);
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#fountainG {
  width: 270px;
  height: 33px;
  margin: auto;
  position: relative;
}

.fountainG {
  background-color: #0ff;
  border-radius: 21px;
  width: 33px;
  height: 33px;
  animation-name: bounce_fountainG;
  animation-duration: 1.3s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
  top: 0;
  transform: scale(.3);
}

#fountainG_1 {
  animation-delay: .52s;
  left: 0;
}

#fountainG_2 {
  animation-delay: .65s;
  left: 34px;
}

#fountainG_3 {
  animation-delay: .78s;
  left: 68px;
}

#fountainG_4 {
  animation-delay: .91s;
  left: 101px;
}

#fountainG_5 {
  animation-delay: 1.04s;
  left: 135px;
}

#fountainG_6 {
  animation-delay: 1.17s;
  left: 169px;
}

#fountainG_7 {
  animation-delay: 1.3s;
  left: 203px;
}

#fountainG_8 {
  animation-delay: 1.43s;
  left: 236px;
}

@keyframes bounce_fountainG {
  0% {
    background-color: #0ff;
    transform: scale(1);
  }

  100% {
    background-color: #0ff;
    transform: scale(.3);
  }
}

@-o-keyframes bounce_fountainG {
  0% {
    background-color: #0ff;
    -o-transform: scale(1);
  }

  100% {
    background-color: #0ff;
    -o-transform: scale(.3);
  }
}

@-webkit-keyframes bounce_fountainG {
  0% {
    background-color: #0ff;
    -webkit-transform: scale(1);
  }

  100% {
    background-color: #0ff;
    -webkit-transform: scale(.3);
  }
}

@-moz-keyframes bounce_fountainG {
  0% {
    background-color: #0ff;
    -moz-transform: scale(1);
  }

  100% {
    background-color: #0ff;
    -moz-transform: scale(.3);
  }
}

